___CSS_LOADER_EXPORT___.push([module.id, `.rcdevsOosModal .modal-container {
	width: fit-content !important;
}

rcdevsOosModalContent,
rcdevsOosModalContent * {
	display: flex;
}

rcdevsOosModalContent {
	flex-flow: column;
	padding: 50px;
	align-items: center;
	justify-content: center;
}

rcdevsOosLogo {
	height: 44px;
	left: 15px;
	position: absolute;
	top: 15px;
	width: 96px;
}

rcdevsOosLogo img {
	object-fit: contain;
}

rcdevsOosModalTitle {
	font-size: 30px;
	font-weight: 700;
}

rcdevsOosModalMainContainer {
	align-items: center;
	flex-flow: column;
	justify-content: center;
}

greenTick {
	display: inline-block;
	font-size: 150px;
	color: green;
	margin-top: 100px;
	margin-bottom: 100px;
}

greenTick img {
	height: 120px;
	margin: auto;
}

.greenTick {
	flex-flow: column;
}

rcdevsOosRow.chosenFile {
	flex-flow: row;
}

rcdevsOosItem.filename {
	font-weight: bold;
	margin-left: 10px;
	font-weight: 600;
}

rcdevsOosRecipientsChoices {
	flex-flow: column;
}

recipientsSignleChoice {
	flex-flow: row;
	margin-bottom: 5px;
}

recipientssignlechoice span {
	white-space: nowrap;
	overflow: hidden;
}

DisplaySelfEmail,
SelectNextcloudUsers,
InputUsersEmails {
	margin-left: auto;
}

displayselfemail,
SelectNextcloudUsers .vs__dropdown-toggle,
recipientsSignleChoice input[type=text] {
	width: 444px;
}

DisplaySelfEmail {
	align-items: center;
}

DisplaySelfEmail span {
	padding-left: 14px;
}

recipientsSignleChoice NcCheckboxRadioSwitch {
	margin-right: auto;
}

InputUsersEmails input {
	height: auto !important;
	margin: 0 !important;
}

rcdevsOosModalFooter {
	align-items: center;
	flex-flow: column;
	justify-content: center;
	margin-top: 50px;
}

button {
	height: fit-content;
}

button.closeModal,
button.submitTransaction {
	min-width: 150px;
	width: fit-content;
	justify-content: center;
	align-items: center;
}

/* ALERTS */
.alert {
	border-radius: .25rem;
	border: 1px solid transparent;
	margin-bottom: 1rem;
	padding: .75rem 1.25rem;
	position: relative;
}

.alertDanger {
	background-color: #f8d7da;
	border-color: #f5c6cb;
	color: #721c24;

}

#errorSettings,
.disabledAction {
	margin-top: 50px;
	width: 400px;
}

.search-results {
	position: absolute;
	margin-top: 50px;
}

.search-results ul {
	flex-flow: column;
	border: 1px solid #ccc;
	border-radius: 16px;
	background-color: white;

}

rcdevsOosSuccess {
	color: var(--color-success-front);
	background-color: var(--color-success-back);
	padding: 0 20px;
}

rcdevsOosError {
	color: var(--color-error-front);
	background-color: var(--color-error-back);
	padding: 0 20px;
}`, "",{"version":3,"sources":["webpack://./src/styles/rcdevsStyle.css"],"names":[],"mappings":"AAAA;CACC,6BAA6B;AAC9B;;AAEA;;CAEC,aAAa;AACd;;AAEA;CACC,iBAAiB;CACjB,aAAa;CACb,mBAAmB;CACnB,uBAAuB;AACxB;;AAEA;CACC,YAAY;CACZ,UAAU;CACV,kBAAkB;CAClB,SAAS;CACT,WAAW;AACZ;;AAEA;CACC,mBAAmB;AACpB;;AAEA;CACC,eAAe;CACf,gBAAgB;AACjB;;AAEA;CACC,mBAAmB;CACnB,iBAAiB;CACjB,uBAAuB;AACxB;;AAEA;CACC,qBAAqB;CACrB,gBAAgB;CAChB,YAAY;CACZ,iBAAiB;CACjB,oBAAoB;AACrB;;AAEA;CACC,aAAa;CACb,YAAY;AACb;;AAEA;CACC,iBAAiB;AAClB;;AAEA;CACC,cAAc;AACf;;AAEA;CACC,iBAAiB;CACjB,iBAAiB;CACjB,gBAAgB;AACjB;;AAEA;CACC,iBAAiB;AAClB;;AAEA;CACC,cAAc;CACd,kBAAkB;AACnB;;AAEA;CACC,mBAAmB;CACnB,gBAAgB;AACjB;;AAEA;;;CAGC,iBAAiB;AAClB;;AAEA;;;CAGC,YAAY;AACb;;AAEA;CACC,mBAAmB;AACpB;;AAEA;CACC,kBAAkB;AACnB;;AAEA;CACC,kBAAkB;AACnB;;AAEA;CACC,uBAAuB;CACvB,oBAAoB;AACrB;;AAEA;CACC,mBAAmB;CACnB,iBAAiB;CACjB,uBAAuB;CACvB,gBAAgB;AACjB;;AAEA;CACC,mBAAmB;AACpB;;AAEA;;CAEC,gBAAgB;CAChB,kBAAkB;CAClB,uBAAuB;CACvB,mBAAmB;AACpB;;AAEA,WAAW;AACX;CACC,qBAAqB;CACrB,6BAA6B;CAC7B,mBAAmB;CACnB,uBAAuB;CACvB,kBAAkB;AACnB;;AAEA;CACC,yBAAyB;CACzB,qBAAqB;CACrB,cAAc;;AAEf;;AAEA;;CAEC,gBAAgB;CAChB,YAAY;AACb;;AAEA;CACC,kBAAkB;CAClB,gBAAgB;AACjB;;AAEA;CACC,iBAAiB;CACjB,sBAAsB;CACtB,mBAAmB;CACnB,uBAAuB;;AAExB;;AAEA;CACC,iCAAiC;CACjC,2CAA2C;CAC3C,eAAe;AAChB;;AAEA;CACC,+BAA+B;CAC/B,yCAAyC;CACzC,eAAe;AAChB","sourcesContent":[".rcdevsOosModal .modal-container {\n\twidth: fit-content !important;\n}\n\nrcdevsOosModalContent,\nrcdevsOosModalContent * {\n\tdisplay: flex;\n}\n\nrcdevsOosModalContent {\n\tflex-flow: column;\n\tpadding: 50px;\n\talign-items: center;\n\tjustify-content: center;\n}\n\nrcdevsOosLogo {\n\theight: 44px;\n\tleft: 15px;\n\tposition: absolute;\n\ttop: 15px;\n\twidth: 96px;\n}\n\nrcdevsOosLogo img {\n\tobject-fit: contain;\n}\n\nrcdevsOosModalTitle {\n\tfont-size: 30px;\n\tfont-weight: 700;\n}\n\nrcdevsOosModalMainContainer {\n\talign-items: center;\n\tflex-flow: column;\n\tjustify-content: center;\n}\n\ngreenTick {\n\tdisplay: inline-